/* Reset default styles */
body {
  margin: 0;
  padding: 0;
  background-color: #f8f9f9;
  font-family: 'Segoe UI', 'Roboto', 'Helvetica Neue', sans-serif;
  color: #333;
}

/* Centered container with padding and shadow */
.container {
  max-width: 600px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: #ffffff;
  border-radius: 16px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.05);
  box-sizing: border-box;
}

/* Header style */
h1 {
  font-size: 2rem;
  color: #2d4836;
  margin-bottom: 1.5rem;
  text-align: center;
}

/* Input styles */
select,
input,
textarea {
  width: 100%;
  padding: 12px;
  margin-bottom: 1rem;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
  box-sizing: border-box;
  background-color: #fff;
}

/* Button styles */
button {
  background-color: #56ab2f;
  color: white;
  border: none;
  padding: 14px 20px;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  width: 100%;
  margin-top: 1rem;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #3f8e1d;
}

/* Loading or additional info text */
p {
  font-size: 1rem;
  color: #444;
  margin: 1rem 0;
}

/* Diagnosis box styles */
.diagnosis-box {
  background-color: #f0f7f1;
  border: 1px solid #cce3d2;
  border-radius: 12px;
  padding: 1.5rem;
  margin-top: 2rem;
  text-align: left;
  white-space: pre-wrap;
  line-height: 1.5;
}

/* Responsive Design */
@media (max-width: 480px) {
  .container {
    padding: 1rem;
    border-radius: 8px;
    margin: 1rem;
  }

  h1 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }

  input,
  select,
  textarea,
  button {
    font-size: 1rem;
  }

  .diagnosis-box {
    padding: 1rem;
    font-size: 0.95rem;
  }
}
